html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: none;
}

.icon-logo {
  width: 5.44em;
  height: 1em;
  fill: black;
}

body {
  /*font-family: 'Charis SIL', arial*/
  font-family: 'Lucida Sans Unicode';
}

.container {
  margin: 0 auto;
  max-width: 960px;
}

.container:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1000px) {
  .container {
    margin: 0 20px;
  }
}

.hero {
  padding-top: 31px;
  padding-bottom: 69px;
  background-repeat: no-repeat;
  position: relative;
  -webkit-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.38);
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.38);
}

.block-shadow {
  position: relative;
}

.block-shadow:before,
.block-shadow:after {
  content: "";
  position: absolute;
  z-index: -1;
  -webkit-box-shadow: 0 2px 40px rgba(0, 0, 0, 0.8);
  -moz-box-shadow: 0 2px 40px rgba(0, 0, 0, 0.8);
  box-shadow: 0 2px 40px rgba(0, 0, 0, 0.8);
  top: 50%;
  bottom: -5px;
  left: 10px;
  right: 10px;
  -moz-border-radius: 800px / 10px;
  border-radius: 800px / 10px;
}

.block-shadow:after {
  right: 10px;
  left: auto;
  -webkit-transform: skew(8deg) rotate(3deg);
  -moz-transform: skew(8deg) rotate(3deg);
  -ms-transform: skew(8deg) rotate(3deg);
  -o-transform: skew(8deg) rotate(3deg);
  transform: skew(8deg) rotate(3deg);
}

.header {
  height: 49px;
  border-bottom: 1px solid #e7e7e2;
  -webkit-box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 0.55);
  -webkit-box-shadow--moz-box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 0.55);
  box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 0.55);
  position: relative;
}

.header__logo {
  float: left;
  margin-left: -1px;
}

.header__phone {
  position: absolute;
  right: 0;
  bottom: 13px;
  font-family: CharisSIL;
  color: #a5a5a5;
  font-size: 18px;
  font-weight: 400;
  line-height: 17px;
}

.header__phone a {
  text-decoration: none;
  letter-spacing: 1px;
  font-family: CharisSIL;
  color: #504f4f;
  font-size: 18px;
  font-weight: 400;
  line-height: 17px;
}

.product {
  margin-top: 45px;
}

.product__img {
  background-color: #fff;
  padding: 10px;
  max-width: 670px;
  max-height: 380px;
  overflow: hidden;
  float: left;
  margin-top: 2px;
  margin-right: 30px;
  box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.38);
}

.product__img img {
  overflow: hidden;
}

.product__text h2 {
  letter-spacing: 2px;
  font-family: 'Charis SIL';
  color: #504f4f;
  font-size: 25px;
  font-weight: bold;
  line-height: 30px;
  text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.55);
  margin-top: -3px;
  margin-bottom: 17px;
}

.product__text p, .product__text ul {
  font-family: LucidaSans;
  color: #505050;
  font-size: 14px;
}

.product__text p {
  letter-spacing: 1.25px;
  line-height: 22px;
  margin-bottom: 4px;
}

.product__text ul {
  letter-spacing: 1.25px;
  line-height: 27px;
  list-style-type: square;
  position: relative;
  left: 14px;
  margin-bottom: 21px;
}

.product__button {
  float: left;
  background: -webkit-linear-gradient(90deg, #9cbdce 0%, #c3dae6 100%);
  background: -moz-linear-gradient(90deg, #9cbdce 0%, #c3dae6 100%);
  background: -o-linear-gradient(90deg, #9cbdce 0%, #c3dae6 100%);
  background: -ms-linear-gradient(90deg, #9cbdce 0%, #c3dae6 100%);
  background: linear-gradient(0deg, #9cbdce 0%, #c3dae6 100%);
}

.product__button a {
  text-decoration: none;
  padding: 9px 18px 9px 18px;
  border: 1px solid #a9bfd2;
  text-transform: uppercase;
  font-family: CharisSIL;
  color: #62889a;
  font-size: 15px;
  line-height: 35px;
}

.slider-box .container {
  display: flex;
  display-flex-direction: column;
  display-justify-content: center;
}

.arrow-left {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
}

.arrow-right {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
}

.slider-box {
  z-index: 5;
  background-color: #c9dce6;
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.22);
}

.slider {
  margin: 0 auto;
  margin-top: 39px;
  margin-bottom: 34px;
}

.slider img {
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.44);
  margin-right: 16px;
  margin-left: 0;
  width: 130px;
  height: 100px;
  padding: 6px;
  background-color: #fff;
}

.slider img:last-child {
  margin-right: 0;
}

.description {
  -webkit-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.38);
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.38);
  background-color: #d7e7ef;
  padding-bottom: 121px;
}

.description .top {
  font-size: 25px;
  line-height: 35px;
  color: #7da0b3;
  font-family: "Charis SIL";
  font-style: italic;
  padding: 55px 0 71px 0;
  letter-spacing: 0.3px;
}

.description .middle div {
  position: relative;
}

.description .middle span {
  position: absolute;
  top: 50%;
  left: 37px;
  transform: translate(0, -32%);
  letter-spacing: 1.1px;
  margin-left: 5px;
  margin-top: -5px;
  font-size: 17px;
  color: #456474;
  font-family: "CharisSIL";
  font-weight: 700;
}

.description .middle-text p {
  letter-spacing: 0.72px;
  margin-top: 5px;
  font-size: 14px;
  line-height: 20px;
  color: #505050;
  font-family: "Lucida Sans";
}

.middle-block {
  width: 290px;
  float: left;
  margin-right: 45px;
}

.middle-block:last-child {
  margin-right: 0;
}

.bottom {
  margin-top: 74px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
}

.bottom__quotes {
  background-color: #c9dce6;
  width: 625px;
  margin-right: 45px;
}

.bottom__quotes > p {
  letter-spacing: 0.35px;
  font-size: 16px;
  line-height: 19px;
  color: #456474;
  font-family: "Charis SIL";
  font-weight: 700;
  margin-top: 12px;
  margin-left: 10px;
  margin-bottom: 20px;
}

.bottom__feedback {
  flex: 1;
}

.bottom__quotes-block {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
}

.bottom__quote {
  width: 263px;
  float: left;
  letter-spacing: 0.16px;
  position: relative;
  margin-left: 33px;
}

.bottom__quote:first-child {
  margin-right: 28px;
}

.bottom__quote span {
  position: absolute;
  top: 0px;
  left: -16px;
  font-size: 50px;
  line-height: 18px;
  color: #456474;
  font-family: "Lucida Sans";
}

.bottom__quote img {
  position: absolute;
  top: -3px;
  left: -22px;
  height: 13px;
  width: 13px;
}

.bottom__quote-text {
  font-style: italic;
  margin-bottom: 6px;
  font-size: 14px;
}

.bottom__quote-author {
  margin-bottom: 16px;
  letter-spacing: 0.5px;
  font-size: 13px;
  font-weight: 600;
}

.bottom__quote-author, .bottom__quote-text {
  line-height: 18px;
  color: #456474;
  font-family: "Lucida Sans";
}

.bottom__feedback-input {
  margin-bottom: 18px;
}

.bottom__feedback-input input[type="text"] {
  float: left;
  border: 1px solid #a9bfd2;
  box-sizing: border-box;
  background-color: #fcfcfc;
  height: 35px;
  width: 194px;
  font-family: LucidaSans;
  color: #7a7a7a;
  font-size: 13px;
  line-height: 35px;
  padding-left: 5px;
  padding-top: 2px;
  letter-spacing: 0.28px;
}

.bottom__feedback-input input[type="submit"] {
  border: 1px solid #a9bfd2;
  border-left: none;
  background: -webkit-linear-gradient(90deg, #9cbdce 0%, #c3dae6 100%);
  background: -moz-linear-gradient(90deg, #9cbdce 0%, #c3dae6 100%);
  background: -o-linear-gradient(90deg, #9cbdce 0%, #c3dae6 100%);
  background: -ms-linear-gradient(90deg, #9cbdce 0%, #c3dae6 100%);
  background: linear-gradient(0deg, #9cbdce 0%, #c3dae6 100%);
  width: 65px;
  height: 35px;
  font-family: CharisSIL;
  color: #62889a;
  font-size: 13px;
  font-weight: 400;
  line-height: 27px;
  text-align: left;
  text-shadow: 0px 1px 0px rgba(255, 255, 255, 0.75);
  text-transform: uppercase;
  text-align: center;
}

.bottom__feedback-contacts {
  float: left;
  width: 100%;
}

.bottom__feedback-contacts img {
  margin-right: 10px;
}

.bottom__feedback-contacts a {
  text-decoration: none;
  letter-spacing: 0.3px;
  font-family: LucidaSans;
  color: #505050;
  font-size: 13.5px;
  line-height: 31px;
  text-align: left;
}

.bottom__feedback-contacts span {
  margin: 0 4px;
}

footer {
  -webkit-box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.68);
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.68);
  background-color: #c9dce6;
}

.copyright {
  letter-spacing: 0.5px;
  float: left;
  font-family: LucidaSans;
  color: #505050;
  font-size: 13px;
  line-height: 46px;
  margin-top: 6px;
  margin-bottom: 0;
}

.copyright p {
  margin: 0;
}

.social-icons {
  float: right;
  margin: 19px 0;
}

.social-icons a {
  margin-left: 4px;
}
